import React, { useState } from "react";
import { Link } from "react-router-dom";
import { TextField, Typography, Grid } from "@material-ui/core";
import { LoginValidation } from "../../validations";
import { getValidationErrors } from "../../../utills/helpers";
import {
  IRootState,
  ILoginPageProps,
  ILoginFormValues,
  ILoginFormErrors,
} from "../../../interfaces";
import { Dispatch } from "redux";
import { requestLogin } from "../../../redux/actions";
import { connect } from "react-redux";
import { RouteURLs } from "../../../routes/routes";
import AppButton from "../../components/Common/AppButton";

const LoginPage: React.FC<ILoginPageProps> = ({
  loginState,
  login,
}: ILoginPageProps) => {
  const [formValues, setFormValues] = useState<ILoginFormValues>({
    email: "",
    password: "",
  });
  const [formErrors, setFormErrors] = useState<ILoginFormErrors>({
    email: "",
    password: "",
  });
  //
  const handleFormvaluesChange = (e: any) => {
    const name = e.target.name as "password" | "email",
      value = e.target.value;
    const newFormValues = Object.assign({}, formValues);
    newFormValues[name] = value;
    setFormValues(newFormValues);
    // unset error message
    const newFormErrors = Object.assign({}, formErrors);
    newFormErrors[name] = "";
    setFormErrors(newFormErrors);
  };
  const handleLogin = (e: any) => {
    e.preventDefault();
    try {
      LoginValidation.validateSync(formValues, {
        abortEarly: false,
      });
      login(formValues);
    } catch (error) {
      setFormErrors(getValidationErrors(error));
    }
  };
  return (
    <>
      <form
        noValidate
        className={"form-container"}
        autoComplete="off"
        onSubmit={handleLogin}
      >
        <TextField
          className={"form-input"}
          name="email"
          label="Email"
          type="email"
          variant="outlined"
          error={!!formErrors.email}
          helperText={formErrors.email}
          value={formValues.email}
          autoComplete="email"
          onChange={handleFormvaluesChange}
          fullWidth
        />
        <TextField
          className={"form-input"}
          name="password"
          label="Password"
          type="password"
          variant="outlined"
          error={!!formErrors.password}
          helperText={formErrors.password}
          value={formValues.password}
          onChange={handleFormvaluesChange}
          fullWidth
        />
        <AppButton
          size="large"
          color="primary"
          type="submit"
          fullWidth
          isLoading={loginState.isLoading}
          text="Login"
        />
        <Grid container className={"form-input"}>
          <Grid item sm={8}>
            <Typography>
              Not a member?&nbsp;
              <Link to={RouteURLs.SIGN_UP}>Register here</Link>.
            </Typography>
          </Grid>
          <Grid item sm={4} className={"text-right"}>
            <Typography>
              <Link to={RouteURLs.LOGIN}>Fotgot password?</Link>
            </Typography>
          </Grid>
        </Grid>
      </form>
    </>
  );
};
const mapStateToProps = (state: IRootState) => ({
  loginState: state.loginReducer,
});
const mapDispatchToProps = (dispatch: Dispatch) => ({
  login: (values: ILoginFormValues) => dispatch(requestLogin(values)),
});
export default connect(mapStateToProps, mapDispatchToProps)(LoginPage);
